









































import { Component, Vue } from 'vue-property-decorator';
import Banner from '../../components/Banner.vue';
import AdvertisingBannerList from '../../components/AdvertisingBannerList.vue';

@Component({
  components: {
    Banner,
    AdvertisingBannerList,
  },
})

export default class NewsContent extends Vue {
  link() {
    if (this.isNotification) this.$router.push('/notification/main');
    else this.$router.go(-1);
  }

  get isNotification() {
    return this.$route.query.process === 'notification';
  }

  advertisingList = [
    {
      id: '1',
      imgUrl: '',
    },
    {
      id: '2',
      imgUrl: '',
    },
    {
      id: '3',
      imgUrl: '',
    },
    {
      id: '4',
      imgUrl: '',
    },
    {
      id: '5',
      imgUrl: '',
    },
  ];

  article = {
    id: '1',
    img: '',
    header: '一銀招考 366 名新進人員',
    date: '2020.07.10',
    from: '金融研訓院',
    click: '221',
    brief: '109年司法人員特考共有19902人報考、海巡特考三等考試則有94...',
    hashtags: ['司法特考', '警察特考'],
  }

  showShare() {
    return navigator.share;
  }

  share() {
    const shareData = {
      url: 'https://pjchender.blogspot.com',
      title: this.article.header,
      text: this.article.brief,
    };
    if (navigator.share) {
      navigator.share(shareData);
    }
  }

  selectTag(tag: string) {
    if (this.isNotification) return;
    this.$router.push({ path: '/main/news', query: { tag } });
  }
}
